import React from 'react'
import styled from 'styled-components'
import 'react-slideshow-image/dist/styles.css'
// import { Fade, Zoom, Slide } from 'react-slideshow-image'
import { Fade } from 'react-slideshow-image'
import {BrowserView, MobileView} from 'react-device-detect';

import Header from '../components/Header'

import fullKitImg from '../assets/full_kit.jpg'

import houtImg from '../assets/Onderdelen/Brandstof/hout.webp'
import pelletsImg from '../assets/Onderdelen/Brandstof/pellets.webp'
import kolenImg from '../assets/Onderdelen/Brandstof/kolen.webp'

import grill1Img from '../assets/Onderdelen/Grill/gril1.jpg'
import grill2Img from '../assets/Onderdelen/Grill/gril2.jpg'
import grill3Img from '../assets/Onderdelen/Grill/gril3.jpg'

import plancha1Img from '../assets/Onderdelen/Plancha/plancha1.jpg'
import plancha2Img from '../assets/Onderdelen/Plancha/plancha2.jpg'
import plancha3Img from '../assets/Onderdelen/Plancha/plancha3.jpg'

import paella1Img from '../assets/Onderdelen/Paellapan/paella1.jpg'
import paella2Img from '../assets/Onderdelen/Paellapan/paella2.jpg'
import paella3Img from '../assets/Onderdelen/Paellapan/paella3.jpg'

import rookdeksel1Img from '../assets/Onderdelen/Rookdeksel/rookdeksel1.jpg'
import rookdeksel2Img from '../assets/Onderdelen/Rookdeksel/rookdeksel2.webp'
import rookdeksel3Img from '../assets/Onderdelen/Rookdeksel/rookdeksel3.webp'

const Body = styled.div`
display: flex;
flex-direction: column;
width:100%;
justify-content: center;
`;

const YoutubeDiv = styled.div`
display: flex;
justify-content: center;
padding: 1em;
`;

const OnderdeelDiv = styled.div`
display: flex;
flex-direction: row; 
width:70%;
justify-content: center;
margin: 2em 0;
align-self: center;

  @media (max-width: 767px) {
    flex-direction: column; 
  }
`;

const OnderdeelDivLeft = styled(OnderdeelDiv)`
  justify-content: start;
`;

const OnderdeelDivRight = styled(OnderdeelDiv)`
  justify-content: end;
`;


const TopDiv = styled.div`
display: flex;
flex-direction: row; 
width:50%;
justify-content: center;
margin: 5em 0 3em 0;
align-self: center;

@media (max-width: 767px) {
  width:80%;
}
`;

const OnderdeelContent = styled.div`
  padding: 0 2em;
`;

const TopContent = styled.div`
  padding: 0.25em 0.75em;
  display: flex;
  flex-direction: column; 
  justify-content: center;

  @media (max-width: 767px) {
    flex-direction: column; 
    margin: 5px;

    width: 100%;
  }
`;

const OnderdeelTitel = styled.h3`
  margin-bottom:0.25em;
  font-size: 1.5em;
`;

const OnderdeelSlideDiv = styled.div`
  width: 24vw;
  margin: 0.5em; 1em;

  @media (max-width: 767px) {
    width: 100%;
  }
`;

const BigOnderdeelSlideDiv = styled.div`
  width: 40vw;
  margin: 0.5em; 1em;

  @media (max-width: 767px) {
    width: 100%;
  }

  h3, p{color:white;}
`;

const Overlay = styled.div`
  
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7); /* Greyish overlay, adjust opacity */
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    color: white;
    font-size: 24px;
    text-align: center;
    border-radius: 35px;
`;

const OnderdeelSlide = styled(Fade)`
  margin:0;
`; 

const OnderdeelSlideImg = styled.div`
  display: flex;
  align-items: start;
  justify-content: center;
  background-size: cover;
  width: 100%;
  padding-bottom: 56.25%; /* 16:9 aspect ratio (9 / 16 = 0.5625) */
  position: relative;
  border-radius: 35px;

  /* This is to ensure the content inside is positioned correctly */
  > * {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
`;

const OnderdeelTxt = styled.p`
margin-top:0em;


font-size: 1.2rem;
`;


const SamenvattingTitel = styled.h3`
  margin-bottom:0.25em;
  font-size: 2.25em;
`;

const SamenvattingTxt = styled.p`
  margin:0;
`;

const SpacerAlign = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  width: 100%;
`;

const Spacer = styled.hr`
  width: 73%;

  border: none;
  border-top: 5px dotted var(--c-tyellow);
  color: #ffff;
  background-color: #ffff;
  height: 1px;
`;

const IntroImg = styled.img`
align-self: center;
display: flex;
align-items: center;
justify-content: center;

height: auto;
width: 20vw;

border-radius: 12px;
`;

const TitDiv = styled.div`
  width: 100%;
  display: flex;
  flex-direction: row; 
  justify-content: center;
`;

const WieTitel = styled.h2`
  font-size: 2em;
  width: 70%;

  margin:0;
  margin-bottom: 0.25em;
  
  line-height: 1.1;
  letter-spacing: -0.01em; 
`;

const Cardholder = styled.div`
  width: 100%;
  align-content: center;

  margin: 1.75em 0 0 0;
  @media (max-width: 767px) {
    margin:0;
    padding: 1em;
  }
`;

const Cardrow = styled.div`
  display: flex;
  align-content: center;
  justify-content: center;
  flex-direction: row;
  gap: 50px;
  
  @media (max-width: 767px) {
    flex-direction: column; 
  }
`;

const CardInner = styled.div`
  width: 100%;
  height: 100%;
  position: relative;
  transform-style: preserve-3d;
  transition: transform 0.999s;
`;

const Card = styled.div`
  width: 460px;
  height: 288px;
  perspective: 1000px;

  margin: 5px;

  &:hover ${CardInner} {
    transform: rotateY(180deg);
  }

  @media (max-width: 767px) {
    flex-direction: column; 
    margin: 5px;

    width: 100%;
    height: auto;
  }
`;

const CardSide = styled.div`
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
`;

const CardFront = styled(CardSide)`
  background-color: var(--c-tblue);
  color: #fff;
  font-weight: bold;
  display: flex;
  align-items: center;
  border: 10px solid var(--c-tblue);
  border-radius: 10px;
  justify-content: center;
  font-size: 24px;
  transform: rotateY(0deg);
`;

const CardBack = styled(CardSide)`
  background-color: var(--c-tyellow);
  color: var(--c-tblue);
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 10px solid var(--c-tyellow);
  border-radius: 10px;
  justify-content: center;
  text-align: center;
  font-size: 18px;
  transform: rotateY(180deg);
}`;

const CardTitleBack = styled.h4`
  margin-top:1em;
  margin-bottom: 0.25em;
`;

const CardTextBack = styled.p`
  margin-top:0;
  margin-bottom: 1em;
`;

const Onderdelenpage = () => {
  const fuelImages = [
    houtImg,
    pelletsImg,
    kolenImg
  ];

  const grilImages = [
    grill1Img,
    grill2Img,
    grill3Img
  ];

  const planchaImages = [
    plancha1Img,
    plancha2Img,
    plancha3Img
  ];

  const paellaImages = [
    paella1Img,
    paella2Img,
    paella3Img
  ];

  const rookdekselImages = [
    rookdeksel1Img,
    rookdeksel2Img,
    rookdeksel3Img
  ];

  return (
    <div>
        <Header/>
        <Body>

          
          <TopDiv>
            
            <TopContent>
              <WieTitel>The Tasty Fire</WieTitel>
                <OnderdeelTxt>
                Multifunctioneel is een groot woord <br/>
Hieronder alvast een oplijsting van de verschillende onderdelen die wij aanbieden met The Tasty Fire. Voor elk onderdeel zijn er standaard drie recepten uitgeschreven door onze kok Emile die variren in moeilijkheidsgraad qua techniek maar ook qua ingredinten.
  <br/><br/>
  Het basispakket zoals hierboven afgebeeld bestaat uit acht verschillende onderdelen en dat start met het aanbieden van de keuze in grondstof voor het vuur;                </OnderdeelTxt>
              </TopContent>
              <BrowserView>
                <IntroImg loading="lazy" src={fullKitImg}/>
              </BrowserView>
          </TopDiv>

          <TitDiv>
            <WieTitel>Ontdek de onderdelen</WieTitel>
          </TitDiv>

          {/* <OnderdeelDivLeft>
            <BigOnderdeelSlideDiv>
            <OnderdeelSlide>
              {fuelImages.map((slideImage, index)=> (
                <div key={index}>
                  <OnderdeelSlideImg key={index} style={{ 'backgroundImage': `url(${slideImage})` }}><OnderdeelContent>
                    <Overlay><OnderdeelTitel>Houtblokken, houtskool of pellets <br/> - </OnderdeelTitel>
                <OnderdeelTxt>
                Je kiest zelf welke grondstof jou voorkeur heeft; The Tasty Fire heeft een grote korf waarin eenvoudig standaard houtblokken voor het vuur kunnen zorgen. Ook een speciaal ontworpen plaat met de nodige verluchting en indeling is voorzien om met houtskool te werken voor het vuur want bepaalde gerechten hebben een specifieke warmte verdeling nodig. Als laatste kan op deze plaat ook met pellets gewerkt worden wanneer we willen roken met onze Tasty Fire.
                </OnderdeelTxt></Overlay>
                
              </OnderdeelContent></OnderdeelSlideImg>
                </div>
              ))}
              
            </OnderdeelSlide>
            </BigOnderdeelSlideDiv>
          </OnderdeelDivLeft>

          <OnderdeelDivRight>
              <BigOnderdeelSlideDiv>
            <OnderdeelSlide>
              {grilImages.map((slideImage, index)=> (
                <div key={index}>
                  <OnderdeelSlideImg key={index} style={{ 'backgroundImage': `url(${slideImage})` }}>
                    <Overlay>

                    <OnderdeelTitel>Grillen <br/> - </OnderdeelTitel>
                <OnderdeelTxt>
                Wanneer we over een BBQ spreken, denken we als echte Belgen automatisch aan een grillrooster met vuur eronder. Ook dit is natuurlijk aanwezig in het basispakket want een lekker stukje vlees, vis of groente op de grill smaakt nog steeds heerlijk. In voedselveilig metaal is ook dit op maat uitgewerkt voor onze Tasty Fire. De richting en breedte van de leggers in combinatie met de handvaten werd zo uitgewerkt zodat zelfs die heerlijke Arrosticini hierop gebakken kunnen worden.                </OnderdeelTxt>
              
                    </Overlay>

                  </OnderdeelSlideImg>
                </div>
              ))}
            </OnderdeelSlide>
            </BigOnderdeelSlideDiv>
          </OnderdeelDivRight>

          <OnderdeelDivLeft>
              <BigOnderdeelSlideDiv>
            <OnderdeelSlide>
              {planchaImages.map((slideImage, index)=> (
                <div key={index}>
                  <OnderdeelSlideImg key={index} style={{ 'backgroundImage': `url(${slideImage})` }}>
                    <Overlay>

                    <OnderdeelTitel>Bakken  la Plancha <br/> - </OnderdeelTitel>
                <OnderdeelTxt>
                Bakken  la plancha of op een Teppanyaki-plaat is als eerste een uiterst gezonde manier van koken. De hoge temperatuur van het metaal zorgt voor een kortere baktijd waardoor er zoveel mogelijk voedingswaardes in het gerecht blijven. We gebruiken veel minder vetten om de ingredinten te bakken.
                <br/><br/>
                Ook hier is de plaat weer zelf ontworpen en stond veiligheid met stip bovenaan de eisenlijst. Doordat de plaat vastligt met zijn uitstekende zijstukken kan deze niet zomaar van onze Tasty Fire schuiven en is er een speciale inkeping voorzien zodat er geen olie of vetten buiten The Tasty Fire kunnen lopen.                
                </OnderdeelTxt>
                    </Overlay>

                  </OnderdeelSlideImg>
                </div>
              ))}
            </OnderdeelSlide>
            </BigOnderdeelSlideDiv>
          </OnderdeelDivLeft>

          <OnderdeelDivRight>
              <BigOnderdeelSlideDiv>
            <OnderdeelSlide>
              {paellaImages.map((slideImage, index)=> (
                <div key={index}>
                  <OnderdeelSlideImg key={index} style={{ 'backgroundImage': `url(${slideImage})` }}>
                    <Overlay>

                    <OnderdeelTitel>Paellapan <br/>-</OnderdeelTitel>
                <OnderdeelTxt>Waarom hoort een Paellapan bij het basispakket? Wel omdat voor ons hiermee alles begon  (het verhaal hierover vind je terug in onze blog)
                <br/><br/>
                Samen koken = samen eten  gedaan met de kok die uren achter het fornuis of aan de BBQ staat en al die gezellige gesprekken moet missen. Dankzij deze formule werkt van jong tot oud samen aan het gerecht en geniet iedereen van de avond.
                <br/><br/>
                De pan bied voor zowel de kok met jaren ervaring als de startende hobbykok de mogelijkheid om voor 15 personen een heerlijk betaalbare maaltijd op tafel te zetten. Wij voorzien verschillende gerechten van snel en eenvoudig (ook ingredinten) tot de complexere varianten waarbij er ook wat moeilijker vindbare ingredinten toegevoegd kunnen worden.    
                </OnderdeelTxt>
                    </Overlay>

                  </OnderdeelSlideImg>
                </div>
              ))}
            </OnderdeelSlide>
            </BigOnderdeelSlideDiv>
          </OnderdeelDivRight>

          <OnderdeelDiv>
              <BigOnderdeelSlideDiv>
            <OnderdeelSlide>
              {rookdekselImages.map((slideImage, index)=> (
                <div key={index}>
                  <OnderdeelSlideImg key={index} style={{ 'backgroundImage': `url(${slideImage})` }}>
                    <Overlay>
                    <OnderdeelTitel>Rookdeksel <br/> - </OnderdeelTitel>
                    <OnderdeelTxt>
                    Het deksel van onze Tasty Fire is niet enkel ter afwerking van het geheel zodat jullie hem in weer en wind buiten kunnen laten staan maar ook als deksel om te roken op onze Tasty Fire. Voorzien van een thermometer, deurtjes en schoorsteen zorgt het deksel ervoor dat je eenvoudig een mooi stuk vlees of vis kan roken. In combinatie met de tussenplaat voor houtskool en/of pellets bereid de hobbykok het mooiste rookgerecht zonder zorgen.
                  </OnderdeelTxt>
                    </Overlay>

                  </OnderdeelSlideImg>
                </div>
              ))}
            </OnderdeelSlide>
            </BigOnderdeelSlideDiv>
          </OnderdeelDiv> */}

{/* blubblubblubblubblubblubblubblubblubblubblubblubblubblubblubblubblubblubblubblubblubblubblubblub */}
          <OnderdeelDiv>
            <OnderdeelSlideDiv>
            <OnderdeelSlide>
              {fuelImages.map((slideImage, index)=> (
                <div key={index}>
                  <OnderdeelSlideImg key={index} style={{ 'backgroundImage': `url(${slideImage})` }}/>
                </div>
              ))}
              
            </OnderdeelSlide>
            </OnderdeelSlideDiv>
            <OnderdeelContent>
                <OnderdeelTitel>Houtblokken, houtskool of pellets</OnderdeelTitel>
                <OnderdeelTxt>
                Je kiest zelf welke grondstof jou voorkeur heeft; The Tasty Fire heeft een grote korf waarin eenvoudig standaard houtblokken voor het vuur kunnen zorgen. Ook een speciaal ontworpen plaat met de nodige verluchting en indeling is voorzien om met houtskool te werken voor het vuur want bepaalde gerechten hebben een specifieke warmte verdeling nodig. Als laatste kan op deze plaat ook met pellets gewerkt worden wanneer we willen roken met onze Tasty Fire.
                </OnderdeelTxt>
              </OnderdeelContent>
          </OnderdeelDiv>

          <SpacerAlign><Spacer/></SpacerAlign>

          <OnderdeelDiv>
            <OnderdeelContent>
              <OnderdeelTitel>Grillen</OnderdeelTitel>
                <OnderdeelTxt>
                Wanneer we over een BBQ spreken, denken we als echte Belgen automatisch aan een grillrooster met vuur eronder. Ook dit is natuurlijk aanwezig in het basispakket want een lekker stukje vlees, vis of groente op de grill smaakt nog steeds heerlijk. In voedselveilig metaal is ook dit op maat uitgewerkt voor onze Tasty Fire. De richting en breedte van de leggers in combinatie met de handvaten werd zo uitgewerkt zodat zelfs die heerlijke Arrosticini hierop gebakken kunnen worden.                </OnderdeelTxt>
              </OnderdeelContent>
              <OnderdeelSlideDiv>
            <OnderdeelSlide>
              {grilImages.map((slideImage, index)=> (
                <div key={index}>
                  <OnderdeelSlideImg key={index} style={{ 'backgroundImage': `url(${slideImage})` }}/>
                </div>
              ))}
            </OnderdeelSlide>
            </OnderdeelSlideDiv>
          </OnderdeelDiv>

          <SpacerAlign><Spacer/></SpacerAlign>

          <OnderdeelDiv>
            <OnderdeelSlideDiv>
            <OnderdeelSlide>
              {planchaImages.map((slideImage, index)=> (
                <div key={index}>
                  <OnderdeelSlideImg key={index} style={{ 'backgroundImage': `url(${slideImage})` }}/>
                </div>
              ))}
            </OnderdeelSlide>
            </OnderdeelSlideDiv>
            <OnderdeelContent>
                <OnderdeelTitel>Bakken  la Plancha</OnderdeelTitel>
                <OnderdeelTxt>
                Bakken  la plancha of op een Teppanyaki-plaat is als eerste een uiterst gezonde manier van koken. De hoge temperatuur van het metaal zorgt voor een kortere baktijd waardoor er zoveel mogelijk voedingswaardes in het gerecht blijven. We gebruiken veel minder vetten om de ingredinten te bakken.
                <br/><br/>
                Ook hier is de plaat weer zelf ontworpen en stond veiligheid met stip bovenaan de eisenlijst. Doordat de plaat vastligt met zijn uitstekende zijstukken kan deze niet zomaar van onze Tasty Fire schuiven en is er een speciale inkeping voorzien zodat er geen olie of vetten buiten The Tasty Fire kunnen lopen.                
                </OnderdeelTxt>
              </OnderdeelContent>
          </OnderdeelDiv>

          <SpacerAlign><Spacer/></SpacerAlign>
          
          <OnderdeelDiv>
          <OnderdeelContent>
              <OnderdeelTitel>Paellapan</OnderdeelTitel>
                <OnderdeelTxt>Waarom hoort een Paellapan bij het basispakket? Wel omdat voor ons hiermee alles begon  (het verhaal hierover vind je terug in onze blog)
                <br/><br/>
                Samen koken = samen eten  gedaan met de kok die uren achter het fornuis of aan de BBQ staat en al die gezellige gesprekken moet missen. Dankzij deze formule werkt van jong tot oud samen aan het gerecht en geniet iedereen van de avond.
                <br/><br/>
                De pan bied voor zowel de kok met jaren ervaring als de startende hobbykok de mogelijkheid om voor 15 personen een heerlijk betaalbare maaltijd op tafel te zetten. Wij voorzien verschillende gerechten van snel en eenvoudig (ook ingredinten) tot de complexere varianten waarbij er ook wat moeilijker vindbare ingredinten toegevoegd kunnen worden.    
                </OnderdeelTxt>          
                </OnderdeelContent>
              <OnderdeelSlideDiv>
            <OnderdeelSlide>
              {paellaImages.map((slideImage, index)=> (
                <div key={index}>
                  <OnderdeelSlideImg key={index} style={{ 'backgroundImage': `url(${slideImage})` }}/>
                </div>
              ))}
            </OnderdeelSlide>
            </OnderdeelSlideDiv>
          </OnderdeelDiv>

          <SpacerAlign><Spacer/></SpacerAlign>

          <OnderdeelDiv>
            <OnderdeelSlideDiv>
            <OnderdeelSlide>
              {rookdekselImages.map((slideImage, index)=> (
                <div key={index}>
                  <OnderdeelSlideImg key={index} style={{ 'backgroundImage': `url(${slideImage})` }}/>
                </div>
              ))}
            </OnderdeelSlide>
            </OnderdeelSlideDiv>
            <OnderdeelContent>
                <OnderdeelTitel>Rookdeksel</OnderdeelTitel>
                <OnderdeelTxt>
                Het deksel van onze Tasty Fire is niet enkel ter afwerking van het geheel zodat jullie hem in weer en wind buiten kunnen laten staan maar ook als deksel om te roken op onze Tasty Fire. Voorzien van een thermometer, deurtjes en schoorsteen zorgt het deksel ervoor dat je eenvoudig een mooi stuk vlees of vis kan roken. In combinatie met de tussenplaat voor houtskool en/of pellets bereid de hobbykok het mooiste rookgerecht zonder zorgen.
              </OnderdeelTxt>
              </OnderdeelContent>
          </OnderdeelDiv>

          <SpacerAlign><Spacer/></SpacerAlign>

                <BrowserView>
          <Cardholder>
              
              <Cardrow>
                <Card>
                  <CardInner>
                    <CardFront>
                      <p>Uitbreiding 1</p>
                    </CardFront>
                    <CardBack>
                    <CardTitleBack>WOKPAN:</CardTitleBack>
                      <CardTextBack>Als allereerste uitbreiding gaan wij een extra grote wokpan (incl. drie recepten) aanbieden om nog meer gerechten op jullie Tasty Fire te kunnen toveren. . coming soon</CardTextBack>
                    </CardBack>
                  </CardInner>
                </Card>
              
                <Card>
                  <CardInner>
                    <CardFront>
                      <p>Uitbreiding 2</p>
                    </CardFront>
                    <CardBack>
                    <CardTitleBack>SURPRISE:</CardTitleBack>
                      <CardTextBack>Wij zitten nog boordevol ideen om de multifunctionaliteit uit te breiden maar testen alles eerst tot in de puntjes zelf uit zodat we elk onderdeel inclusief de nodige gerechten met 100% zekerheid van kwaliteit en bruikbaarheid aan jullie kunnen presenteren.
<br/><br/>
Zijn jullie benieuwd naar wat we aan het testen zijn? Op onze blogpagina lichten wij regelmatig een tipje van de sluier </CardTextBack>
                    </CardBack>
                  </CardInner>
                </Card>
              </Cardrow>

            </Cardholder>
            </BrowserView>

          <OnderdeelDiv>
          <div>
          <SamenvattingTitel>Samenvatting</SamenvattingTitel>  
          <SamenvattingTxt>We hebben hier ook een korte demo video waar we onszelf en natuurlijk ook de TastyFire voorstellen.</SamenvattingTxt>      
          <YoutubeDiv>
            <iframe loading="lazy" width="360vw" height="640vw" src="https://www.youtube.com/embed/aKeowa_xe-w" title="Who are we, and what is The Tasty Fire?" frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" allowFullScreen></iframe>
          </YoutubeDiv>
          </div>
          </OnderdeelDiv>
        </Body>
    </div>
  )
}

export default Onderdelenpage
