import React from 'react'
import styled, { keyframes } from 'styled-components';
import { Link } from 'react-router-dom';

const OnloadAnimation = keyframes`
    0% {
        opacity: 0;
        transform: translateY(3%);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
`;

const CardDiv = styled.div`
	display: flex;
	flex-wrap: wrap;
	align-items: flex-start;
	flex-wrap: wrap;
	justify-content: center;
	gap: 2.5rem;
	width: 90%;
	max-width: 100%;
	margin: 2vh auto;

    /* Media query for screens with a maximum width of 767px (typical for mobile devices) */
    @media (max-width: 767px) {
        margin: 1vh auto;
    }

    opacity: 0;
    animation: ${OnloadAnimation} 1s ease-out forwards;
`;

const AnimatedCardDiv = styled(CardDiv)`
  &:nth-child(1) {
    animation-delay: 0.4s;
  }

  &:nth-child(2) {
    animation-delay: 0.2s; 
  }

  &:nth-child(3) {
    animation-delay: 0s;
  }
`

const Card = styled.article`
    border-radius: 16px;
    box-shadow: 0 30px 30px -25px rgba(65, 51, 183, 0.25);
    width: 100%;

    padding: 1.5em 2.5em;

    /* Apply transition to background-color and color */
    transition: 350ms;

    background-color: var(--c-tlyellow);

    &:hover{
        transform: translateY(-10px); /* scale(1.2)*/
    }
`;

const CardTitle = styled.h3`
    font-size: 1.5rem;
    line-height: 1.1;
    letter-spacing: -0.01em; 
    margin:0;

    text-align:center;
`;

const CardInfo = styled.p`
    margin-top: .5em;
    margin-bottom: 1em;

    text-align:center;
`;

const IconDiv = styled.div`
    width:100%;
    margin-bottom:1em;
`;

const CardIcon = styled.img`
    width:80%;
    height:auto;

    margin-left: auto;
    margin-right: auto;
    display: block;

    
`;

const OptionCard = ({options}) => {

    return (
            <AnimatedCardDiv>
                <Card>
                    <IconDiv>
                    <CardIcon loading="lazy"  src={options[2]}></CardIcon>
                    </IconDiv>
                    <CardTitle>{options[0]}</CardTitle>
                    <CardInfo>{options[1]}</CardInfo>

                </Card>
            </AnimatedCardDiv>
    )
}

export default OptionCard
