import React from 'react'
import styled from 'styled-components'
import { Link } from 'react-router-dom';
import {BrowserView, MobileView} from 'react-device-detect';

import tf_ring_logo from '../assets/tf_ring_logo.png'
import cropped_kit_img from '../assets/total_kit_cropped.jpg'
import mobile_kit_img from '../assets/full_kit_mobile.jpg'
import bbq_image from '../assets/bbq_image.jpg'
import forkImg from '../assets/icons/fork.png'
import fireplaceImg from '../assets/icons/fireplace.png'
import bbqImg from '../assets/icons/bbq.png'
import weatherImg from '../assets/icons/weather.png'

import OptionCard from '../components/OptionCard';

const HeaderImgDiv = styled.div`

`;

const HeaderImg = styled.img`
    width:100%;
    height: auto;
    overflow: hidden;
`;

const LogoImg = styled.img`
    width:auto;
    height: 15em;;
    display: block;
    margin-left: auto;
    margin-right: auto;
    transform: translate(0, 1px);
`;

const Header = styled.header`
    text-align: center;
    position: relative;
`;

const MenuDiv = styled.div`
    position: absolute;
    bottom: 0;
    width: 100%;

    box-shadow: 0 30px 30px -25px rgba(65, 51, 183, 0.25);
`;

const NavDiv = styled.div`
    background-color: var(--c-tblue);
    padding: 1.5em;
`;

const Navigation = styled.nav`
  display: flex;
  justify-content: center;

  height:100%;
  width:100%;
`;

const NavigationList = styled.ul`
  list-style: none;
  margin: 0;
  padding: 0.5em;

  display: flex;
  justify-content: space-evenly;
`;

const NavigationItem = styled.li`
  display: inline-block;
  margin-left: 40px;

  font-size: 1.5rem;

`;

const NavLink = styled(Link)`
  color: white;
  font-weight: bold;
  transition: all 0.4s ease-in-out;
  text-decoration: none;

  display: block;
  position: relative;
  padding: 0.2em 1em;

  overflow: hidden;

  &:hover {
    color: var(--c-tlbrown);
  };
`;

const Section = styled.section`
  padding: 2em;

  justify-content: center;
  display: flex;
  flex-direction: row;
`;

const InfoDiv = styled.div`
  width:80%;
  padding:0;
  justify-content: center;
  display: flex;
  flex-direction: row;
`;

const ClassicDiv = styled.div`
  width:80%;

`;

const CardDeck = styled.div`
    display: flex;
    justify-content: center;
    gap: 2.5rem;
    width: 90%;
    max-width: 1200px;
    margin: 0vh auto;

    /* Media query for screens with a maximum width of 767px (typical for mobile devices) */
    @media (max-width: 767px) {
        flex-direction: column; /* Stack cards vertically on mobile */
        align-items: center; /* Center cards horizontally within the container */
        gap: 1rem;
    }
`;

const CardHolder = styled.div`
  width:100%;

  display: flex;
  justify-content: center;
  flex-direction: row;

  gap: 5%;
`;

const TextCard = styled.div`
  padding: 3em;
  width: 100%;
`;

const BBQImageDiv = styled.div`
    width: 90%;
    padding: 1em;

    margin-top: auto;
    margin-bottom: auto;
`;

const BBQImage = styled.img`
    width: 90%;
    height: auto;
    object-fit: contain;

  border-radius: 6px;
  box-shadow: 0 30px 30px -25px rgba(65, 51, 183, 0.25);
`;

const Button = styled.button`
    background-color: var(--c-tyellow);
    border: none;
    border-radius: 10rem;
    color: white;
    font-weight: 600;
    pointer-events: all;
    cursor: pointer;
    transition: all 0.4s ease-in-out;
    box-shadow: inset 0 0 0 0.09px var(--c-tblue);
  
    display: flex;
    align-items: center;
    gap: 15px;
    padding: 16px 40px;
    font-size: 1em;
  
    &:hover {
        box-shadow: inset 250px 0 0 0 var(--c-tblue);
        transform: scale(1.1);
    }
`;

const Title = styled.h2`
    margin: 0;
    font-size: 3em;

    line-height: 1.1;
    letter-spacing: -0.01em; 

     /* Media query for screens with a maximum width of 767px (typical for mobile devices) */
    @media (max-width: 767px) {
       font-size: 2.5em;
    }
`;

const Subtitle = styled.h3`
    color: var(--c-tyellow);
    margin:0;
    font-size: 1.5em;

    margin-bottom: 1em;
`;

const Txt = styled.p`
    margin:0;
`;

const ForYouTxt = styled.p`
    font-weight: bold;
`;

const CheckTitel = styled.h2`
    font-size: 2em;
    line-height: 1.1;
    letter-spacing: -0.01em; 
`;

const OntdekLink = styled(Link)`
    text-decoration: none;
`;

const OntdekButton = styled(Button)`
    margin-top: 3em;
`;

const CenterDiv = styled.div`
    display: flex;
    flex-direction: row;
    justify-content: center;
`;

const QDiv = styled.div`
    width: 98%;
`;

const QDivTxt = styled.p`
    font-size: 1.5em;
    margin:0.5em;
`;

const QDivLeftTxt = styled(QDivTxt)`
    margin-right: 50%;
`;

const QDivRightTxt = styled(QDivTxt)`
    text-align: end;
    margin-left: 50%;
`;

const ButtonDiv = styled.div`
    margin-top:2em;
    display: flex;
    flex-direction: row;
    justify-content: center;
`;

const SpacerAlign = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  width: 100%;
`;

const Spacer = styled.hr`
  width: 85%;

  border: none;
  border-top: 3px dotted var(--c-tyellow);
  color: #ffff;
  background-color: #ffff;
  height: 1px;
`;

const Homepage = () => {

    const optionList = [
        ["Multifunctioneel", "Van braden en grillen tot wokken, hij kan het allemaal. Achteraf transformeer je heb in een gezellige vuurkorf.", forkImg],
        ["Weerbestendig", "The Tasty Fire kan in weer en wind, zomer en winter gewoon buiten blijven staan. Dat bespaart weer plaats in de garage of het tuinhuis en een hoop sleurwerk.", weatherImg],
        ["Kies uw brandstof", "Gebruikt u graag kolen, hout of pellets? The Tasty Fire is compatibel met ze allemaal.", fireplaceImg],
        ["Stijlvol", "Van braden en grillen tot wokken, hij kan het allemaal", bbqImg],
    ];

  return (
    <div>
        <Header>
            <BrowserView>
            <HeaderImgDiv>
                <HeaderImg src={cropped_kit_img} alt='Tasty Fire Kit' loading="lazy"/>
            </HeaderImgDiv>
            <MenuDiv>
                
                <LogoImg src={tf_ring_logo} alt='Tasty Fire Logo' loading="lazy"/>
                
                <NavDiv>
                    <Navigation>
                        <NavigationList>
                        <NavigationItem>
                            <NavLink to="/">Home</NavLink>
                        </NavigationItem>
                        <NavigationItem>
                            <NavLink to="/Onderdelen">Onderdelen</NavLink>
                        </NavigationItem>
                        <NavigationItem>
                            <NavLink to="/About">Over ons</NavLink>
                        </NavigationItem>
                        <NavigationItem>
                            <NavLink to="/Blog">Blog</NavLink>
                        </NavigationItem>
                        <NavigationItem>
                            <NavLink to="/Contact">Contact/Bestellen</NavLink>
                        </NavigationItem>
                        </NavigationList>
                    </Navigation>
                </NavDiv>
            </MenuDiv>
            </BrowserView>
            <MobileView>
                <HeaderImg src={mobile_kit_img} alt='Tasty Fire Kit' loading="lazy"/>
            </MobileView>
        </Header>

        <body>
            <Section>
            <InfoDiv>
                <TextCard>
                    <h2>FUN  FIRE  FOOD  FRIENDS</h2>
                    <Title>De Multifunctionele Barbecue</Title>
                    <Subtitle>n oplossing voor al je BBQ noden</Subtitle>
                    <Txt>Ontdek het veelzijdige buitenkookconcept voor gezellige momenten met familie en vrienden - onze duurzame BBQ is meer dan een aanwinst voor je tuin...<br/><br/>
Imponeer met gemak een gevarieerd menu, zonder dat je een koksopleiding nodig hebt. Laat The Tasty Fire het hele jaar buiten staan, weerbestendig en ruimtebesparend. De ideale metgezel voor jouw huishouden - ontdek verrassende mogelijkheden met deze unieke vuurkorfbarbecue.</Txt>
                <OntdekLink to="/Onderdelen"><OntdekButton>Ontdek hem nu</OntdekButton></OntdekLink>
                </TextCard>
                <BBQImageDiv>
                    <BBQImage src={bbq_image} alt='Tasty Fire Logo' loading="lazy"/>
                </BBQImageDiv>
                </InfoDiv>
            </Section>

            <Section>
            <ClassicDiv>
                <CheckTitel>Waarom kiezen voor The Tasty Fire?</CheckTitel>
                <CardDeck>
                    {optionList.map((option,index) => (
                            <OptionCard options={option} key={option[0]}/>
                        ))}
                </CardDeck>
                </ClassicDiv>
            </Section>

            <Section>
                <ClassicDiv>
                    <CheckTitel>Is de Tasty Fire Barbecue iets voor u?</CheckTitel>
                   
                    <CenterDiv>
                        <QDiv>
                            <QDivTxt>Op zoek naar een toestel waarmee je vrienden en familie met een gevarieerd menu, van voorgerecht tot dessert, kan verbazen zonder dat je hiervoor een koksopleiding voor moet volgen?</QDivTxt>

                            <SpacerAlign><Spacer/></SpacerAlign>

                            <QDivTxt>Ben je het beu om alle fun en straffe verhalen aan tafel te missen omdat jij achter die kookpotten of grill moet staan?</QDivTxt>
                            
                            <SpacerAlign><Spacer/></SpacerAlign>

                            <QDivTxt>Ben je op zoek naar een BBQ die je in weer en wind buiten kan laten staan en waarvoor je geen speciaal plaatsje in je tuinhuis moet reserveren tijdens de winter?</QDivTxt>
                            

                            <QDivTxt></QDivTxt>
                            {/* <QDivLeftTxt>Op zoek naar een toestel waarmee je vrienden en familie met een gevarieerd menu, van voorgerecht tot dessert, kan verbazen zonder dat je hiervoor een koksopleiding voor moet volgen?</QDivLeftTxt>
                            <QDivRightTxt>Ben je het beu om alle fun en straffe verhalen aan tafel te missen omdat jij achter die kookpotten of grill moet staan?</QDivRightTxt>
                            <QDivLeftTxt>Ben je op zoek naar een BBQ die je in weer en wind buiten kan laten staan en waarvoor je geen speciaal plaatsje in je tuinhuis moet reserveren tijdens de winter?</QDivLeftTxt>
                            <QDivRightTxt><ForYouTxt>De Tasty Fire is helemaal iets voor u! Ontdek meteen alle onderdelen!!</ForYouTxt></QDivRightTxt> */}
                        </QDiv>
                        
                    </CenterDiv>

                    <ButtonDiv>
                        <NavLink to="/Onderdelen"><Button>Ontdek hem nu!</Button></NavLink>
                    </ButtonDiv>

                </ClassicDiv>
            </Section>
        </body>

    </div>
  )
}

export default Homepage
