import React from 'react'
import styled from 'styled-components';
import {BrowserView, MobileView} from 'react-device-detect';

import Header from '../components/Header'

import anneSophieImg from '../assets/about/anne-sophie.webp'
import louiseImg from '../assets/about/louise.jpg'
import emileImg from '../assets/about/emile.webp'
import famImg from '../assets/about/familie.webp'

import anneSophieMobImg from '../assets/about/anne-sophie_cropped.webp'
import louiseMobImg from '../assets/about/louise_cropped.jpg'
import emileMobImg from '../assets/about/emile_cropped.webp'

const Body = styled.div`
display: flex;
flex-direction: column;
width:100%;
justify-content: center;
`;


const IntroDiv = styled.div`
display: flex;
flex-direction: column; 
width:70%;
justify-content: center;
margin: 2em 0;
align-self: center;
`;

const OnderdeelDiv = styled.div`
display: flex;
flex-direction: row; 
width:70%;
justify-content: center;
margin: 2em 0;
align-self: center;

@media (max-width: 767px) {
    flex-direction: column;  
  }
`;

const IntroImg = styled.img`
align-self: center;
display: flex;
align-items: center;
justify-content: center;

height: auto;
width: 25vw;

border-radius: 12%;

@media (max-width: 767px) {
    width: 90%; 
  }
`;

const OnderdeelContent = styled.div`
  padding: 0.25em 1.5em;
`;

const OnderdeelTitel = styled.h3`
  margin-bottom:0;
  font-size: 1.75em;
`;

const OnderdeelSlideImg = styled.img`
  display: flex;
  align-items: center;
  justify-content: center;
  background-size: cover;

  height: 23vw;

  border-radius: 10%;
`;

const OnderdeelTxt = styled.p`
margin-top:0em;
`;

const SpacerAlign = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  width: 100%;
`;

const Spacer = styled.hr`
  width: 73%;

  border: none;
  border-top: 5px dotted var(--c-tyellow);
  color: #ffff;
  background-color: #ffff;
  height: 1px;
`;

const SubTitel = styled.h4`
  color: var(--c-tyellow);
  margin:0;
  margin-bottom: 1em;
`;

const CenterTxtDiv = styled.div`
  text-align: center;
`;

const IntroTextTitle = styled.h2`
  margin-top: 0.5em;
  margin-bottom: 0.25em;
`;

const IntroText = styled.p`
  margin-top: 0;
`;

const TitDiv = styled.div`
  width: 100%;
  display: flex;
  flex-direction: row; 
  justify-content: center;
`;

const WieTitel = styled.h2`
  font-size: 2em;
  width: 70%;

  margin:0;
  line-height: 1.1;
  letter-spacing: -0.01em; 
`;

const Aboutpage = () => {
  return (
    <div>
      <Header/>

      <Body>
        
          <IntroDiv>
          <IntroImg loading="lazy" src={famImg}/>
          <CenterTxtDiv>
            <IntroTextTitle>The Tasty Fire Family</IntroTextTitle>
            <IntroText>Samen als broer en twee zussen een onderneming opstarten is voor vele geen evidentie maar voor ons was het vanzelfsprekend. Als gezin zijn we namelijk opgegroeid met The Tasty Fire centraal in onze tuin waarrond steeds de mooiste momenten plaatsvinden. Ons enthousiasme om op The Tasty Fire te koken is zo groot dat wij in 2023 besloten om deze multifunctionele BBQ met jullie te delen.</IntroText>
          </CenterTxtDiv>
          
          </IntroDiv>

          <TitDiv>
            <WieTitel>Wie zijn wij?</WieTitel>
          </TitDiv>
          

          <OnderdeelDiv>
            <BrowserView>
              <OnderdeelSlideImg loading="lazy" src={anneSophieImg}/>
            </BrowserView>
            
                
            <MobileView>
              <OnderdeelSlideImg loading="lazy" src={anneSophieMobImg}/>
            </MobileView>

            <OnderdeelContent>
                <OnderdeelTitel>Anne-Sophie</OnderdeelTitel>
                <SubTitel>Marketing</SubTitel>
                <OnderdeelTxt>
                Als oudste zus ben ik een echt familiemens! Mijn vrije momenten samen met onze familie (en mijn hond Ryno) doorbrengen is voor mij pure ontspanning. Samen koken, zeker in de buitenlucht, is in mijn ogen n van de mooiste activiteiten die je samen kunt doen. Het houdt gesprekken met een zware ondertoon op de achtergrond, iedereen, ook onze oma, kan eraan deelnemen en het resultaat vult niet alleen de buik, het prikkelt ook de smaakpapillen.
                <br/><br/>
                Cijfers en moeilijke zinnen zijn minder mijn ding dus samen met mijn broer, focus ik mij op onze marketing en sociale media.
                </OnderdeelTxt>
              </OnderdeelContent>
          </OnderdeelDiv>

          <SpacerAlign><Spacer/></SpacerAlign>

          <OnderdeelDiv>
              <MobileView>
                <OnderdeelSlideImg loading="lazy" src={louiseMobImg}/>
              </MobileView>

            <OnderdeelContent>
                <OnderdeelTitel>Louise</OnderdeelTitel>
                <SubTitel>Administratie</SubTitel>
                <OnderdeelTxt>
                Als middelste van de drie ben ik misschien wel de meest stille, figuurlijk gezien, maar ook wel de meest kritische van het gezin. Geef mij maar het duidelijke en betrouwbare van cijfers en letters. Ik hou me op liever op de achtergrond bezig met alle administratie maar ben wel als eerste aanwezig wanneer mijn broer een nieuw gerecht wil testen en breng dan graag een lekkere wijn mee.
                <br/><br/>
                Lekker eten appreciren is iets wat ik met volwassen worden geleerd heb. Als kind was eten een noodzakelijk kwaad maar ondertussen staat eten voor mij gelijk aan gezelligheid en ontspanning. Geef mij een lekker stukje vlees of een Aziatisch gerecht en ik ben de gelukkigste vrouw van Limburg.
                </OnderdeelTxt>
              </OnderdeelContent>

              <BrowserView>
                <OnderdeelSlideImg loading="lazy" src={louiseImg}/>
              </BrowserView>
              
          </OnderdeelDiv>

          <SpacerAlign><Spacer/></SpacerAlign>

          <OnderdeelDiv>
            <BrowserView>
              <OnderdeelSlideImg loading="lazy" src={emileImg}/>
            </BrowserView>
            
            <MobileView>
              <OnderdeelSlideImg loading="lazy" src={emileMobImg}/>
            </MobileView>
                
            
            <OnderdeelContent>
                <OnderdeelTitel>Emile</OnderdeelTitel>
                <SubTitel>Culinair Expert</SubTitel>
                <OnderdeelTxt>
                Als professionele kok vind ik het ook in mijn vrije tijd uitdagend om nieuwe gerechten te bedenken. Na een lange werkdag hebben de meeste onder ons wel zin in een gezellig feestje maar slechts weinigen in het vele voorbereidend werk dat vaak in een gerecht kruipt. Daarin wil ik met onze gerechten het grote verschil maken. Het moet haalbaar zijn op alle vlakken van het koken en dat start zelfs met inkopen doen in de supermarkt tot het dresseren op het bord.
                <br/><br/>
                Naast het koken is fotografie een hobby die ik voor The Tasty Fire graag terug onder het stof uitgehaald heb. Deze combinatie maakt dat ik graag allerlei mooie en lekkere dingen deel met jullie in onze sociale media.
                </OnderdeelTxt>
              </OnderdeelContent>
          </OnderdeelDiv>

      </Body>
      
    </div>
  )
}

export default Aboutpage
