import React from 'react'
import styled from 'styled-components'
import { Link } from 'react-router-dom';
import {BrowserView, MobileView} from 'react-device-detect';

import tf_ring_logo from '../assets/tf_logo_w_ring.png'

const LogoImg = styled.img`
    width:auto;
    height: 7.5em;
    display: block;
    margin-left: 0;
    margin-right: auto;
    align-self: center;
`;

const HeaderDiv = styled.header`

    justify-content: center;
    display: flex;
    flex-direction: row;

    box-shadow: 0 30px 30px -25px rgba(65, 51, 183, 0.25);
    background-color: var(--c-tblue);
`;

const NavDiv = styled.div`
    background-color: var(--c-tblue);
    padding: 2.25em;
`;

const Navigation = styled.nav`
  display: flex;
  justify-content: center;

  height:100%;
  width:100%;
`;

const NavigationList = styled.ul`
  list-style: none;
  margin: 0;
  padding: 0.75em;

  display: flex;
  justify-content: space-evenly;
`;

const NavigationItem = styled.li`
  display: inline-block;
  margin-left: 30px;

`;

const NavLink = styled(Link)`
  color: white;
  font-weight: bold;
  font-size: 1.25rem;
  transition: all 0.4s ease-in-out;
  text-decoration: none;

  display: block;
  position: relative;
  padding: 0.1em 0;

  overflow: hidden;

  &:hover {
    color: var(--c-tlbrown);
  };
`;

const LogoDiv = styled.div`
  display: flex;
  flex-direction: row;
`;

const LogoTxtDiv = styled.div`
  display: flex;
  flex-direction: column;  
  justify-content: center;

  margin-left:0.5em;
`;

const LogoTitel = styled.h2`
color: var(--c-tyellow);
margin:0;

font-size: 1.75em;
`;

const LogoSubTitel = styled.h5`
color: var(--c-tyellow);
margin:0;
font-weight: 300;

font-size: 1em;
`;

const HomeLink = styled(Link)`
 text-decoration: none;
`;

const Header = () => {
  return (
    <div>
      <BrowserView>
      <HeaderDiv> 
            
              <HomeLink to="/">
                <LogoDiv>
                  <LogoImg src={tf_ring_logo} alt='Tasty Fire Logo' loading="lazy"/>
                  <LogoTxtDiv>
                    <LogoTitel>THE TASTY FIRE</LogoTitel>
                    <LogoSubTitel>Outdoor Cooking Experience</LogoSubTitel>
                  </LogoTxtDiv>
                </LogoDiv>
                </HomeLink>

                
                <NavDiv>
                    <Navigation>
                        <NavigationList>
                        <NavigationItem>
                            <NavLink to="/">Home</NavLink>
                        </NavigationItem>
                        <NavigationItem>
                            <NavLink to="/Onderdelen">Onderdelen</NavLink>
                        </NavigationItem>
                        <NavigationItem>
                            <NavLink to="/About">Over ons</NavLink>
                        </NavigationItem>
                        <NavigationItem>
                            <NavLink to="/Blog">Blog</NavLink>
                        </NavigationItem>
                        <NavigationItem>
                            <NavLink to="/Contact">Contact/Bestellen</NavLink>
                        </NavigationItem>
                        </NavigationList>
                    </Navigation>
                </NavDiv>
                
        </HeaderDiv>
      </BrowserView>
    </div>
    
  )
}

export default Header
