import React from 'react'
import styled from 'styled-components';

import Header from '../components/Header';
import {BrowserView, MobileView} from 'react-device-detect';

import fullKitImg from '../assets/full_kit.jpg'

const CardHolder = styled.div`
    width: 100%;
`;

const CardDiv = styled.div`
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;

    align-self: center;
`;

const Card = styled.div`
    width: 70%;
    display: flex;
    flex-direction: column;
    justify-content: center;

    align-self: center;
    
    margin: 2em 0;
`;

const CardBestel = styled(Card)`
  flex-direction: row;
  gap: 3em;
`;

const CardTitle = styled.h2`
margin-bottom:0;
`;

const Image = styled.img`
  border-radius: 25px;
  width: 100%;
  height:auto;

  @media (max-width: 767px) {
     width: 90%; 
  }
`;

const IncentiveTxt = styled.p`
    margin-top:0;
    margin-bottom:1em;
`;

const CardForm = styled.form`
    display: flex;
    flex-direction: column;
    justify-content: center;
`;

const Label = styled.label`
    margin-bottom: 0.25em;
    font-weight: bold;
`;

const Input = styled.input`
  margin-bottom: 15px;
  margin-right:5px;

  border-radius: 10px;
  outline: 2px solid #333;
  border: 0;
  background-color: #fff;
  outline-offset: 1px;
  padding: 10px 1rem;
  transition: outline 0.25s ease;

  &:hover {
    outline: 2px solid var(--c-tyellow);
    transition: outline 0.25s ease;
  }

  &:focus {
    outline-offset: 5px;
    outline: 2px solid (--c-tblue);;
    transition: outline 0.25s ease;
  }
`;

const ReqLabel = styled(Label)`
  &:after{
    content:" *";
    color: red;
  }
`;

const RadioLabel = styled.label`

`;

const RadioButton = styled.input`
  background-color: var(--c-tblue);
`;

const TextArea = styled.textarea`
margin-bottom: 15px;
  margin-right:5px;

  border-radius: 10px;
  outline: 2px solid #333;
  border: 0;
  background-color: #fff;
  outline-offset: 1px;
  padding: 10px 1rem;
  transition: outline 0.25s ease;

  &:hover {
    outline: 2px solid var(--c-tyellow);
    transition: outline 0.25s ease;
  }

  &:focus {
    outline-offset: 5px;
    outline: 2px solid (--c-tblue);;
    transition: outline 0.25s ease;
  }
`;

const Button = styled.button`
background-color: var(--c-tyellow);
    border: none;
    border-radius: 10rem;
    color: white;
    font-weight: 600;
    pointer-events: all;
    cursor: pointer;
    transition: all 0.4s ease-in-out;
    box-shadow: inset 0 0 0 0.09px var(--c-tblue);
  
    gap: 15px;
    padding: 16px 40px;
    font-size: 1em;
    text-align: center;
  
    &:hover {
        box-shadow: inset 3000px 0 0 0 var(--c-tblue);
        transform: scale(1.01);
    }
`;

const CardDivider = styled.div`
   width: 100%;
`;

const PrijsTxt = styled.p`
    font-weight: bold;
    font-size: 1.5em;
`;

const BrowserViewScaled = styled(BrowserView)`
  width:50%;
`;

const ExtraDiv = styled.div`
  display: flex;
  flex-direction: row;

  @media (max-width: 767px) {
        flex-direction: column;
    }
`;

const Contactpage = () => {
  return (
    <div>
    <Header/>

    <CardHolder>
        <CardDiv>
            <CardBestel>
              <CardDivider>
                <CardTitle>Bestellen</CardTitle>
                <IncentiveTxt>Volledig overtuigd? Klaar om onze multifunctionele weersbestendige BBQ te kopen? Ga ervoor!  Prijs =  2410 incl. BTW</IncentiveTxt>
                <CardForm>
                    <ReqLabel for="name">Naam</ReqLabel>
                    <Input type="text" id="name" name="name" placeholder='Typ uw naam hier...' required/>

                    <ReqLabel for="email">E-mail</ReqLabel>
                    <Input type="email" id="email" name="email" placeholder='Typ uw e-mail hier...' required/>
                    
                    <Label for="phone">Telefoon nr.</Label>
                    <Input type="tel" id="phone" name="phone" placeholder='Type telefoon nummer hier...'/>

                    <ExtraDiv>
                    <Label>Extra opties:</Label><br/>
                    <input type="radio" id="none" name="opties" value="none" checked/>
                    <RadioLabel for="none">Geen extras</RadioLabel>
                    <RadioButton type="radio" id="wokpan" name="opties" value="wokpan"/>
                    <RadioLabel for="wokpan">Wokpan (incl. 3 extra recepten)</RadioLabel>
                    </ExtraDiv>
                    <br/>

                    <PrijsTxt>Uw Tasty Fire kost:  2140,00 incl. BTW</PrijsTxt>

                    <Button type="submit">Bestel</Button>

                </CardForm>
                </CardDivider>

                <BrowserViewScaled>
                  <CardDivider>
                    <Image loading="lazy" src={fullKitImg}/>
                  </CardDivider>
                  </BrowserViewScaled>

            </CardBestel>
        </CardDiv>

        <MobileView>
          <Image loading="lazy" src={fullKitImg}/>
        </MobileView>

        <CardDiv>
            <Card>
                <CardTitle>Contact</CardTitle>
                <IncentiveTxt>Twijfelt u nog of heeft u een vraag? Bent u geinterresseerd in een showcooking event waarin onze huiskok Emile u alle functies van The Tasty Fire komt uitleggen door gezellig wat te koken? Aarzel dan niet om een bericht te sturen!</IncentiveTxt>
                <CardForm>
                    <ReqLabel for="name">Naam</ReqLabel>
                    <Input type="text" id="name" name="name" placeholder='Typ uw naam hier...' required/>

                    <ReqLabel for="email">E-mail</ReqLabel>
                    <Input type="email" id="email" name="email" placeholder='Typ uw e-mail hier...' required/>
                    
                    <Label for="phone">Telefoon nr.</Label>
                    <Input type="tel" id="phone" name="phone" placeholder='Type telefoon nummer hier...'/>

                    <ReqLabel for="message">Boodschap</ReqLabel>
                    <TextArea id="message" name="message" rows="8" placeholder='Deel uw mening of stel uw vraag hier...' required></TextArea>

                    <Button type="submit">Verzenden</Button>

                </CardForm>
            </Card>
        </CardDiv>
    </CardHolder>
    </div>
  )
}

export default Contactpage
