import React from 'react'
import styled from 'styled-components'

import { Link } from 'react-router-dom';

import instaImg from '../assets/social_media/instagram_w.png'
import linkedinImg from '../assets/social_media/linkedin_w.png'
import facebookImg from '../assets/social_media/facebook_w.png'
import youtubeImg from '../assets/social_media/youtube_w.png'

const FooterDiv = styled.div`
  background-color: var(--c-tblue);
  color: white;

  font-size: 1em;
  
  padding:1.2em;
  margin-top: auto;

  p {
    margin:0.3em;
    display: flex;
    justify-content: right;
  }
`;

const SamplicationsLink = styled(Link)`
  text-decoration: none;
  color: white;
`;

const SocialDiv = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
    gap: 3em;
    padding: 0.75em;

    @media (max-width: 767px) {
      gap: 10%;
   }
`;

const SocialButton = styled.a`
    background-color: var(--c-tyellow);
    border-radius:50%;

    width: 3em;
    height: 3em;

    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    overflow: hidden;

    &:hover{
        background-color: var(--c-tbrown);
        transition: all 0.4s ease-in-out;
        transform: scale(1.1);
    }
`;

const SocialButtonImg = styled.img`
    width: 50%; 
    height: auto;
`;

const Footer = () => {

  const handleButtonClick = (url) => {
    window.open(url, '_blank');

  };

  return (
    <FooterDiv>
        {/* Follow us on */}
      <SocialDiv>
        <SocialButton onClick={() => handleButtonClick('https://www.instagram.com/thetastyfire/')}><SocialButtonImg src={instaImg}/></SocialButton>
        <SocialButton onClick={() => handleButtonClick('https://www.linkedin.com/company/thetastyfire')}><SocialButtonImg src={linkedinImg}/></SocialButton>
        <SocialButton onClick={() => handleButtonClick('https://www.facebook.com/TheTastyFire')}><SocialButtonImg src={facebookImg}/></SocialButton>
        <SocialButton onClick={() => handleButtonClick('https://www.youtube.com/@TheThreeofUsbv')}><SocialButtonImg src={youtubeImg}/></SocialButton>

      </SocialDiv>
      {/* <SamplicationsLink to='https://samplications.com/' target="_blank"><p>Website by Samplications</p></SamplicationsLink> */}
    </FooterDiv>
  )
}

export default Footer
